body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header{
  background-color: black;
  color:white;
  padding:20px 70px;
  display:flex;
  justify-content: space-between;
}
.cartIcon .counter{
  position:relative;
  right:5px;
  top:-5px;
  height:18px;
  width:18px;
  background-color: blue;
  display:flex;
  align-items:center;
  justify-content: center;
  border-radius:50%;
}
.cartIcon{
  display:flex;
}
.icon{
  font-size:22px;
}
img{
  height:150px;
}
.cart-wrapper{
  display:inline-flex;
  
  box-shadow:0px 0px 7px black;
  width:300px;
border-radius:5px;
}
.image-wrapper{
  display:flex;
  align-items: center;
  justify-content: center;
  padding:10px;
  border-right:1px solid rgba(128, 128, 128, 0.885);
}
.content{
  display:flex;
  flex-direction:column;
  flex-grow:1;
  gap:10px;
  padding:10px;
}
.price{
  margin-bottom: auto;
}
.content button{
  background-color: black;
  color:white;
  border-radius: 3px;
}
.quantity{
  display:flex;
  align-self: flex-end;
  border-radius:3px;
  border:1px solid grey;
}
.quantity div{
  height:25px;
  width:25px;
  display:flex;
  align-items:center;
  justify-content: center;
}
.quantity>div:first-child,.quantity>button{
  background-color: blue;
  color:white;
  border-radius:0;
}
.quantity>button:disabled{
  opacity:0.7;
}
main{
padding:20px;
display:flex;
flex-wrap:wrap;
min-height:70vh;
justify-content:center;
align-items:center;
align-content: center;
gap:30px;
}
footer{
  background-color: gray;
  color:black;
  margin-bottom: none;
  padding:17px 50px;
  box-sizing:border-box;
  position:fixed;
  width:100%;
  bottom:0;
  text-align:right;
}